@import url(https://fonts.googleapis.com/css2?family=Darker+Grotesque:wght@800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Darker+Grotesque:wght@800&display=swap);
html, body{
  
  overflow-x: hidden;
  
}


body {
  margin: 0;
  font-family: 'Darker Grotesque', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #1e1a2b;
  position: relative;
  overflow-y: hidden;
}



/* @import url('https://fonts.googleapis.com/css2?family=Raleway:wght@700;900&display=swap'); */

* {
  box-sizing: border-box;  
}

.body{
    text-align: left;
    font-size: 40px;
}

.title{
    text-align: left;
    font-size:50px;
    color: white;
}

.whiteText{
    color: white;
}

.whitespace{
    height: 10vh;
}




.ourPhoto{
    /* transform: scale(1.5);
    transition: all .5s; */
    -webkit-animation: fadeIn ease 2s;
            animation: fadeIn ease 2s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    transform: scale(1);
    transition: all .3s ease-in-out;
}

.ourPhoto:hover{
    transform: scale(1.05);
    transition: all .5s ease-in-out;
}

.bgEffect{
    -webkit-animation:slide 5s ease-in-out infinite alternate;
            animation:slide 5s ease-in-out infinite alternate;
    background-image: linear-gradient(-60deg, #f2791d 50%, #ff2b2b 50%);
    left:-50%;
    opacity:.5;
    position: absolute;
    right:-50%;
    z-index:-1;
    height: 900px;
}

.overlay1{
    animation-direction:alternate-reverse;
    -webkit-animation-duration:6s;
            animation-duration:6s;
}

.overlay2{
    -webkit-animation-duration:7s;
            animation-duration:7s;
}

#responsive{
    max-width: 100%;
    height: auto;
}

#hoverDetails{
    background-color:#00F;

    /* filter: blur(0.15rem); */
    /* background: rgba(242, 121, 29,0.85); */

}

@-webkit-keyframes slide {
    0% {
      transform:translateX(-25%);
    }
    100% {
      transform:translateX(25%);
    }
  }

@keyframes slide {
    0% {
      transform:translateX(-25%);
    }
    100% {
      transform:translateX(25%);
    }
  }

@-webkit-keyframes fadeIn {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

@keyframes fadeIn {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

@media only screen and (min-width: 800px){
    #burgerMenu{
        visibility: hidden;
    }
}

@media only screen and (max-width: 800px){
    #headerBarLinks{
        visibility: hidden;

    }
}
